
.card-img{
    width: 280px;
    height: 240px;
    object-fit: cover;
  }
  .card-img img{
    width: 100%;
    height: 100%;
    object-fit: contain;
  }

  .card{
    margin: 10px 15px;
    min-height: 200px;
    width: 280px;
    height: 100%;
    border-radius: 4px;
    box-shadow: 0px 0px 3px rgba(0,0,0,0.25);
    background-color: #fff;
    overflow: hidden;  
    transition: all 0.3s ease-in-out;
    cursor: pointer;
  }
  
  .card:hover{
    box-shadow: 0px 4px 16px rgba(0,0,0,0.35);
    margin-bottom: 15px;
  }
  
  .card-detail{
    padding: 12px 8px;
  }